import React from 'react'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faUser,
  faMapMarkerAlt,
  faUserTie,
  faUserGroup,
  faPhone,
  faEnvelope,
  faLock,
} from "@fortawesome/free-solid-svg-icons";
import JobSelect from './JobSelect';


export default function InputGroupDet() {
  return (
    <div>

<div className="inputcontainer">
        <FontAwesomeIcon icon={faUser} className="icon" />
        <input type="text" placeholder="User_Group_ID:" />
      </div>

      <div className="inputcontainer">
        <FontAwesomeIcon icon={faMapMarkerAlt} className="icon" />
        <input type="text" placeholder="Group_Name" />
      </div>

      <div className="inputcontainer">
        <FontAwesomeIcon icon={faPhone} className="icon" />
        <input type="text" placeholder="Group_Description" />
      </div>

      <div className="inputcontainer">
        <FontAwesomeIcon icon={faEnvelope} className="icon" />
        <input type="text" placeholder="Relevant_Privileges" />
      </div>

      <div className="inputcontainer">
        <FontAwesomeIcon  className="icon" />
      </div>
      <JobSelect/>

    
      
    </div>
  )
}
